import React, { Component } from 'react'

import { Spinner, Beabcrumb, Alert, ModalForm } from 'mzara-component'
import ButtonSpinner from '../../component/btn/ButtonSpinner'

class _Home extends Component {
    state = {

    }

    lang = null
    navigation = null

    constructor(props){

        super(props)

        this.state = {
            is_load: true,
        }
    }

    componentWillMount(){

    }

    componentWillUnmount(){

    }

    componentWillUpdate(nextProps, nextState){

    }

    componentDidMount(){
        //Todo : faire les recherches d'évaluation ici d'abord
        this.setState({ is_load: true })
    }

    get_beabcrumb_data(){
        let result = []

        //Home
        result.push({
            label: this.props.lang.line('std_home'),
            href: this.props.ajax.site_url(''),
        })

        return result
    }

    render() {

        const { 
            is_load, 
            is_submiting, 
            login_err,
            is_show_modal_form,
            data_modal_form,
        } = this.state

        return (
            <div className="">

                <div className="row">
                    <div className="col-12">
                        <div className="page-header">
                            <Beabcrumb
                                data_list={this.get_beabcrumb_data()}
                                />
                        </div>
                    </div>

                    <div className="col-12 mt-30">
                        <div className="box box-white" style={{ height: '320px' }}>
                            <h3>{this.props.lang.line('std_maps')}</h3>
                        </div>
                    </div>
                </div>

                {
                    is_show_modal_form &&
                    <ModalForm
                        ajax={this.props.ajax}
                        lang={this.props.lang}
                        data={data_modal_form}
                        />
                }
            </div>
        )
    }
}

export default _Home


