import React from 'react'

const ButtonBlock = (props) => (
	<button 
		type="button"
		className={ "btn " + (!props.unblock ? 'btn-warning' : 'btn-primary') + ' ' + props.className}
		onClick={props.onClick}>
		<i className={"fa " + (!props.unblock ? 'fa-ban' : 'fa-user-tie')}></i>
		{
			props.text
		}
	</button>
)

export default ButtonBlock


