import AccountController from '../account'

 import { StringHelper, Utils } from 'mzara-library'

class Card {
    ajax = null

    card_name = ''
    card_structure = null
    list = []
    line_list = null
    count = 0
    metadata_list = {}
    metadata = null
    index = 0
    nb = 10
    is_last = false
    list_change_callback_list = []
    data_change_callback_list = []
    sort_list = null

    data = null

    constructor(props){
        this.ajax = props.ajax
        this.lang = props.lang

        this.refresh_callback = props.refresh_callback

        this.props = props

        // this.props.hub.add_event_listner(this.handle_hub_change)
        //todo : hub event listener for list_change, data_change
    }

    set_card_name(value){
        this.card_name = value
    }

    is_sending_structure_request
    get_data_structure(callback){

        if (this.card_structure === null && !this.is_sending_structure_request) {

            this.is_sending_structure_request = true

            this.props.ajax.send_request({
                url: 'structure/' + this.card_name,
                type: 'GET',
                data_type: 'json',
                done: (xhr, response) => {

                    this.is_sending_structure_request = false

                    if (xhr.status === 200) {
                        this.card_structure = response

                        if (callback) {
                            callback()
                        }
                    }
                 
                }
            })

            return null
        }

        return this.card_structure
    }

//List
    is_sending_request = false
    get_list({key, order_by, sort, debut}, is_getting_more){

        if (is_getting_more && !this.is_sending_request) {

            this.is_sending_request = true

            this.props.ajax.send_request({
                url: this.card_name,
                type: 'GET',
                data: { key, order_by, sort, nb: this.nb, debut: debut },
                data_type: 'json',
                done: (xhr, response) => {

                    this.is_sending_request = false

                    if (xhr.status === 200) {
                        // this.index = this.index + this.nb

                        if (response.data_list.length === 0) {
                            this.is_last = true
                        }
                        else{
                            this.count = response.count
                            response.data_list.map((data) => {
                                this.append_data(data)
                            })
                        }
                        
                        this.dispatch_list_change()
                    }

                    if (xhr.status === 500 || xhr.status === 400) {
                        // this.refresh('internal_error')
                    }                    
                }
            })
        }

        return this.get_page_list(this.nb, debut)
    }

    get_count(){
        return this.count
    }

    append_data(data){

        let should_push = true

        this.list.map((item) => {
            if (item.id === data.id) {
                should_push = false
            }
        })

        if (should_push) {
            this.list.push(data)
        }
    }

    get_page_list(nb, debut){

        let result = []

        for (var i = debut; i < debut + nb; i++) {
            if (this.list[i] === undefined) {
                break;
            }

            result.push(this.list[i])
        }

        return result
    }

    clear_list(){
        this.index = 0
        this.is_last = false
        this.list = []
    }

    clear_metadata(){
        this.metadata = null
        this.line_list = null
    }

    is_last(){
        return this.is_last
    }

    get_sort_data_list(callback){

        if (this.sort_list === null) {
            this.props.ajax.send_request({
                url: 'sort/' + this.card_name,
                type: 'GET',
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    this.sort_list = response
                    callback(response)

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.sort_list
    }

    get_edit_form(data){
        const data_structure = Utils.get_clone(this.get_data_structure())
        let has_id = false

        data_structure.data_list.map((_data) => {
            for(let index in data){

                if (_data.name === 'id') {
                    has_id = true
                }

                if (_data.name === index) {
                    _data.value = data[index]
                }
            }
        })

        if (!has_id ) {
            data_structure.data_list.push({
                type: 'hidden',
                name: 'id',
                value: data.id,
            })
        }

        return data_structure
    }

    create(data, success, error){
        this.props.ajax.send_request({
            url: this.card_name,
            type: 'POST',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.dispatch_list_change()
                    success(response)
                }

                if (xhr.status === 500 || xhr.status === 400) {
                    let message = ''
                    for(let index in response.messages){
                        message = response.messages[index]
                    }
                    error(message)
                }
            }
        })
    }

    update(data, success, error){
        //update_list
        for(let index in data){
            this.metadata[index] = data[index]
        }

        this.dispatch_data_change()

        this.props.ajax.send_request({
            url: this.card_name,
            type: 'PUT',
            data: data,
            content_type: 'json',
            data_type: 'json',
            // before_send: () => { before_send() },
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.props.ajax.set_cache(this.card_name  + '/' + this.metadata.id, 'GET', this.metadata)
                    success()
                }

                // if (xhr.status === 500 || xhr.status === 400) {
                //     error(response.messages.error)
                // }
            }
        })
    }

    read(id){

        console.log('read', id, this.metadata)

        if (this.metadata === null) {
            this.props.ajax.send_request({
                url: this.card_name + '/' + id,
                type: 'GET',
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    if (xhr.status === 200) {
                        this.metadata = response
                        this.dispatch_data_change(response)
                    }

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.metadata
    }

    delete(id, success, error){

        this.props.ajax.send_request({
            url: this.card_name,
            type: 'DELETE',
            data: { id },
            data_type: 'json',
            content_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 204) {
                    success()
                }

                if (xhr.status === 500 || xhr.status === 400) {
                    error(response.messages.error)
                }
            }
        })
    }

    validate(id, success, error){

        this.props.ajax.send_request({
            url: 'validate/' + this.card_name,
            type: 'POST',
            data: { id },
            data_type: 'json',
            content_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.metadata = response
                    success(response)
                }

                if (xhr.status === 500 || xhr.status === 400) {
                    error(response.messages.error)
                }
            }
        })
    }

    access(callback){

        this.props.ajax.send_request({
            url: 'access/' + this.card_name,
            type: 'GET',
            data_type: 'json',
            cache: true,
            done: (xhr, response) => {

                if (xhr.status === 200) {
                    callback(response)
                }

            }
        })
    }

    is_sending_line_request = false
    get_line_list(quotation_id, is_getting_more){

        if (this.line_list === null) {

            this.is_sending_line_request = true

            this.props.ajax.send_request({
                url: this.card_name + '/line/' + quotation_id,
                type: 'GET',
                data_type: 'json',
                done: (xhr, response) => {

                    this.is_sending_line_request = false

                    if (xhr.status === 200) {
                        this.line_list = response
                        
                        this.dispatch_data_change(response)
                    }               
                }
            })

            return []
        }

        return this.line_list
    }

    get_line_form(data, data_structure, quotation_id){

        let has_id = false

        data_structure.data_list.map((_data) => {
            for(let index in data){

                if (_data.name === 'id') {
                    has_id = true
                }

                if (_data.name === index) {
                    _data.value = data[index]
                }
            }

            if (data.reset) {
                _data.value = undefined
            }
        })

        if (!has_id ) {
            data_structure.data_list.push({
                type: 'hidden',
                name: 'id',
                value: data.id,
            })
        }

        if (quotation_id !== undefined) {
            data_structure.data_list.push({
                type: 'hidden',
                name: 'quotation_id',
                value: quotation_id,
            })
        }

        return data_structure
    }

    get_line(id){
        let result = null

        this.line_list.map((line, index) => {
            if (line.id === id) {
                result = line
            }
        })

        return result
    }

    get_line_index(id){
        let result = null

        this.line_list.map((line, index) => {
            if (line.id === id) {
                result = index
            }
        })

        return result
    }

    line_create(data, success, error){
        this.props.ajax.send_request({
            url: this.card_name + '/line',
            type: 'POST',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {

                    this.line_list.push(response)

                    this.dispatch_data_change()
                    success(response)
                }
            }
        })
    }

    line_update(data, success, error){

        const line = this.get_line(data.id)
        for(let index in data){
            line[index] = data[index]
        }

        this.props.ajax.send_request({
            url: this.card_name + '/line',
            type: 'PUT',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.dispatch_data_change()
                    success(response)
                }
            }
        })
    }

    line_delete(data, success, error){

        this.props.ajax.send_request({
            url: this.card_name + '/line',
            type: 'DELETE',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 204) {

                    const line_index = this.get_line_index(data.id)
                    this.line_list.splice(line_index, 1)

                    this.dispatch_data_change()
                    success(response)
                }
            }
        })
    }

    get_line_resume(metadata){

        if (this.line_list === null) {
            return {}
        }

        let prix_total_ht = 0
        let tva = metadata.tva

        this.line_list.map((line) => {

            let _total = line.price_unit_ht * line.qte
            let _remise = line.reduction.includes('%') ? (_total) * (parseFloat(StringHelper.to_numeric(line.reduction)) / 100) : (parseFloat(StringHelper.to_numeric(line.reduction)))
            let _prix_remise = _total - _remise
            prix_total_ht += _prix_remise
        })

        let remise = metadata.reduction
        remise = remise !== undefined ? remise : '0'
        let prix_remise = ((remise != '' ? 
            (remise.includes('%') ? (prix_total_ht) * (parseFloat(StringHelper.to_numeric(remise)) /100) : (parseFloat(StringHelper.to_numeric(remise))) ) 
            : 0));
        
        let prix_total_remise = prix_total_ht - prix_remise;
        let prix_total_tva = prix_total_remise * (tva / 100);
        let prix_ttc = prix_total_remise + prix_total_tva;

        return {
            prix_total_ht: prix_total_ht,
            prix_total_remise: prix_total_remise,
            prix_total_tva: prix_total_tva,
            prix_ttc: prix_ttc,
            prix_ttc_litteral: StringHelper.to_literral(prix_ttc, this.props.lang.get_local())
        }
    }

    get_price_unit_index(structure_data_list){
        let result = 0

        structure_data_list.map((item, index) => {
            if (item.name === 'price_total') {
                result = index
            }
        })

        return result
    }

    get_btn_cmd(data_structure){
        if (data_structure === null) {
            return []
        }
        
        let result = []
        data_structure.data_list.map((data) => {
            if (data.type === 'hidden' && data.extra === 'command') {
                result = data.data_list
            }
        })

        return result
    }

    //Event_listener
    add_event_listner(event_name, _function){
        if (event_name === undefined) {
            return false
        }

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.push(_function);
                break;

            case 'data_change':
                this.data_change_callback_list.push(_function);
                break;

        }
    }

    remove_event_listner = (event_name, _function) => {

        if (event_name === undefined) {
            return false
        }

        var index_catched = null

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.list_change_callback_list.splice(index_catched, 1)
                break;

            case 'data_change':
                this.data_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.data_change_callback_list.splice(index_catched, 1)
                break;
        }
    }

    dispatch_list_change(param){
        this.list_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }

    dispatch_data_change(param){
        this.data_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }
}


export default Card


