import React from 'react'

const ButtonAdd = (props) => (
	<button 
		type="button"
		className={ "btn btn-primary btn-sm btn-add " + props.className}
		onClick={props.onClick}>
		<i className="fa fa-plus-circle"></i>
		{ props.text }
	</button>
)

export default ButtonAdd


