import React from 'react'

import ButtonSpinner from './ButtonSpinner'

const ButtonMore = (props) => (
	<button className={"btn btn-default btn-block mb-15 " + (props.is_submiting ? 'disabled' : '') + ' ' + props.className} type="button">
        {
        	props.text === undefined
        }
        {
            props.is_submiting &&
            <ButtonSpinner /> 
        }
    </button>
)

export default ButtonMore


