class UserRole {
    ajax = null

    list = []
    metadata_list = {}
    metadata = null
    index = 0
    nb = 10
    is_last = false
    list_change_callback_list = []
    data_change_callback_list = []
    sort_list = null

    data = null

    constructor(props){
        this.ajax = props.ajax
        this.lang = props.lang

        this.refresh_callback = props.refresh_callback

        this.props = props

        // this.props.hub.add_event_listner(this.handle_hub_change)
        //todo : hub event listener for list_change, data_change
    }

//List
    is_sending_request = false
    get_list({key, order_by, nb, sort}, is_getting_more, callback){

        if (is_getting_more && !this.is_sending_request) {

            if (nb !== undefined) {
                this.nb = nb
            }

            this.is_sending_request = true

            this.props.ajax.send_request({
                url: 'role',
                type: 'GET',
                data: { key, order_by, sort, nb: this.nb, debut: this.index },
                data_type: 'json',
                // before_send: () => { before_send() },
                done: (xhr, response) => {

                    this.is_sending_request = false

                    if (xhr.status === 200) {
                        this.index = this.index + this.nb

                        if (response.length === 0) {
                            this.is_last = true
                        }
                        else{
                            response.map((data) => {
                                this.list.push(data)
                            })
                        }
                        
                        this.dispatch_list_change()

                        if (callback !== undefined) {
                            callback(response)
                        }
                    }

                    if (xhr.status === 500 || xhr.status === 400) {
                        // this.refresh('internal_error')
                    }                    
                }
            })
        }

        return this.list
    }

    clear_list(){
        this.index = 0
        this.is_last = false
        this.list = []
    }

    is_last(){
        return this.is_last
    }

    //Event_listener
    add_event_listner(event_name, _function){
        if (event_name === undefined) {
            return false
        }

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.push(_function);
                break;

            case 'data_change':
                this.data_change_callback_list.push(_function);
                break;

        }
    }

    remove_event_listner = (event_name, _function) => {

        if (event_name === undefined) {
            return false
        }

        var index_catched = null

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.list_change_callback_list.splice(index_catched, 1)
                break;

            case 'data_change':
                this.data_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.data_change_callback_list.splice(index_catched, 1)
                break;
        }
    }

    dispatch_list_change(param){
        this.list_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }

    dispatch_data_change(param){
        this.data_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }

    get_select_list(callback, is_getting_more){

        this.get_list({ order_by: 'designation', sort: 'asc', nb: 100 }, is_getting_more === undefined, (data_list) => {
            let result = []
            data_list.map((data, index) => {
                if (data.id != 2) {
                    result.push({
                        value: data.id,
                        label: data.designation,
                        commentaire: data.commentaire,
                    })
                }
            })

            callback(result)
        })

    }

    get_sort_data_list(callback){

        if (this.sort_list === null) {
            this.props.ajax.send_request({
                url: 'sort/role',
                type: 'GET',
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    this.sort_list = response
                    callback(response)

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.sort_list
    }

    create(data, success, error){
        this.props.ajax.send_request({
            url: 'role',
            type: 'POST',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.dispatch_list_change()
                    success(response)
                }

                if (xhr.status === 500 || xhr.status === 400) {
                    let message = ''
                    for(let index in response.messages){
                        message = response.messages[index]
                    }
                    error(message)
                }
            }
        })
    }

    read(id){
        if (this.metadata === null) {
            this.props.ajax.send_request({
                url: 'role/' + id,
                type: 'GET',
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    if (xhr.status === 200) {
                        this.metadata = response
                        this.dispatch_data_change(response)
                    }

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.metadata
    }

    update(data, success, error){
        //evaluate role
        data.rules = {}
        this.metadata.argument_list.map((argument) => {
            data.rules[argument.value] = data[argument.value]
            data[argument.value] = undefined
        })

        //update_list
        for(let index in data){
            this.metadata[index] = data[index]
        }

        this.props.ajax.send_request({
            url: 'role',
            type: 'PUT',
            data: data,
            content_type: 'json',
            data_type: 'json',
            // before_send: () => { before_send() },
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.props.ajax.set_cache('role/'+ this.metadata.id, 'GET', this.metadata)
                    success()
                }

                // if (xhr.status === 500 || xhr.status === 400) {
                //     error(response.messages.error)
                // }
            }
        })
    }

    delete(id, success, error){

        this.props.ajax.send_request({
            url: 'role',
            type: 'DELETE',
            data: { id: id },
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 204) {
                    //update_metadata
                    this.metadata = undefined
                    return success()
                }

                return error()
            }
        })
    }

    access(callback){

        this.props.ajax.send_request({
            url: 'access/role',
            type: 'GET',
            data_type: 'json',
            cache: true,
            done: (xhr, response) => {

                if (xhr.status === 200) {
                    callback(response)
                }

            }
        })
    }

    get_create_form(){
        return {
            title: this.props.lang.line('std_add_user'),
            data_list: [
                {
                    label: this.props.lang.line('std_designation'),
                    type: 'text',
                    name: 'designation',
                    id: 'designation',
                    placeholder: this.props.lang.line('std_designation'),
                    is_required: true,
                },
                {
                    label: this.props.lang.line('std_commentaire'),
                    type: 'textarea',
                    name: 'commentaire',
                    id: 'commentaire',
                    placeholder: this.props.lang.line('std_commentaire'),
                },
            ]

        }
    }

}


export default UserRole


