import React, { Component } from 'react'
import { connect } from 'react-redux'

import { Spinner } from 'mzara-component'
// import { Account as AccountController } from 'mzara-controller'
import CardController from '../../controller/card'

import Home from './Home'
import Read from './Read'

import './css/style.css'

class Main extends Component {
    state = {

    }

    navigation = null

    constructor(props){
        super(props)

        this.state = {
            method: props.method,
            param: props.param
        }

        this.navigation = props.navigation

        this.init_controller()
    }

    componentWillReceiveProps(nextProps){
        this.setState(function(prevState, props){
            return {
                method: nextProps.method,
                param: nextProps.param
            };
        })
    }

    componentWillMount(){

    }

    componentWillUnmount(){

    }

    componentWillUpdate(nextProps, nextState){

    }

    componentDidMount(){
        
    }

    init_controller(){
        this.controller = new CardController({
            ajax: this.props.ajax,
            lang: this.props.lang,
            hub: this.props.hub,

            refresh_callback: (e, data) => this.refresh(e, data),
        })

        this.controller.set_card_name(this.state.method)
    }


    load_home(method){

        return (
            <Home 
                hub={this.props.hub}
                lang={this.props.lang}
                ajax={this.props.ajax}
                card_name={method}
                navigation={this.props.navigation}
                controller={this.controller}
                />
        )
    }

    load_read(method, id){

        return (
            <Read 
                hub={this.props.hub}
                lang={this.props.lang}
                ajax={this.props.ajax}
                navigation={this.props.navigation}
                card_name={method}
                id={id}
                is_self_trigger={true}
                controller={this.controller}
                />
        )
    }

    render_default(){
        return (
            <div className="main-container">
                <Spinner position="center" />
            </div>
        )
    }

    render() {

        const { method, param } = this.state

        if (param.id !== undefined) {
            return this.load_read(method, param.id)
        }else{
            return this.load_home(method)
        }

    }
}

const mapStateToProps = (state) => {
    return {
        ajax: state.ajax,
        lang: state.lang,
        hub: state.hub
    }
}

export default connect(mapStateToProps)(Main)


