import React, { Component } from 'react'

import { Dropdown, Image, Paragraph } from 'mzara-component'
import { DOMHelper, StringHelper } from 'mzara-library'
import Mustache from 'mustache'

class ListItem extends Component {
    state = {

    }

    lang = null
    navigation = null
    gallery_component = null

    constructor(props){
        super(props)

        this.state = {
            is_load: !props.is_self_trigger,
            id: props.id,
            data: props.data,
            header_list: props.header_list,
            is_self_trigger: props.is_self_trigger,
            layout: props.layout,
            onClick: props.onClick,
        }

        if (props.is_self_trigger) {
            this.controller = props.controller
        }
    }

    componentWillMount(){

    }

    componentWillUnmount(){

    }

    componentWillReceiveProps(nextProps){
        this.setState({ data: nextProps.data, layout: nextProps.layout, header_list: nextProps.header_list })
    }

    componentWillUpdate(nextProps, nextState){
        
    }

    componentDidUpdate(){

    }

    componentDidMount(){
        if (this.state.is_self_trigger) {
            this.controller.get_metadata((data) => {
                this.setState({ data: data, is_load: true })
            })
            // this.get_metadata(this.state.tag_link)
        }
    }

    handle_item_click = (e) => {
        e.preventDefault()

        if (!DOMHelper.hasClass(e.target, 'btn') && this.state.onClick !== undefined) {
            this.state.onClick(this.state.data, this, e)
        }
    }

    refresh(){
        this.forceUpdate()
    }

    render() {

        const { 
            is_load,
            header_list,
            data,
            layout,
            is_submiting,
            is_simple,
        } = this.state

        return(
            <div 
                className={"fiche-item product-item"}
                onClick={this.handle_item_click}>
                <div className="row">
                    <div className={ !is_simple ? "col-9 col-md-8" : 'col-12'}>
                        <div>
                            <a href={ this.props.ajax.site_url('product/'+ data.id) }>
                                <Image
                                    ajax={this.props.ajax}
                                    navigation={this.navigation}
                                    className=""
                                    style={{ height: 120, width: 120 }}
                                    src={this.props.ajax.site_url('image/'+ data.image_id)} />
                            </a>
                            <h1><a href={ this.props.ajax.site_url('product/'+ data.id) }>{data.name}</a></h1>
                            <Paragraph
                                className="produit-description"
                                lang={this.props.lang}
                                string_limit={200}
                                is_should_show_more_link={false}
                                is_show_limited={true}
                                text={data.description}/>
                            
                        </div>
                    </div>
                    <div className="col-3 col-md-4">
                        <div className="produit-price-info">
                            <h6 className="text-medium text-bold text-right">Ar {StringHelper.number_format(data.price, 2, ',', ' ')}</h6>
                            <h6 className="text-small text-gray text-right text-line-through mb-5">Ar {StringHelper.number_format(data.price_before, 2, ',', ' ')}</h6>
                        </div>
                    </div>
                    
                </div>

            </div>
        )
    }
}

export default ListItem


