import React, { Component } from 'react'

import { Dropdown, ModalForm } from 'mzara-component'
import { StringHelper } from 'mzara-library'
import Mustache from 'mustache'

import ButtonEdit from '../../../component/btn/ButtonEdit'
import ButtonTooltip from '../../../component/btn/ButtonTooltip'

class Table extends Component {
    state = {

    }

    lang = null
    navigation = null
    gallery_component = null

    constructor(props){
        super(props)

        this.state = {
            quotation_id: props.quotation_id,
            quotation_data: props.quotation_data,
            metadata: props.metadata === undefined ? {} : props.metadata,
            card_name: props.card_name,
        }
    }

    componentWillMount(){

    }

    componentWillUnmount(){

    }

    componentWillReceiveProps(nextProps){
        if (nextProps.metadata === undefined) {
            return false
        }
        this.setState({ 
            quotation_id: nextProps.quotation_id,
            quotation_data: nextProps.quotation_data,
            metadata: nextProps.metadata,
            card_name: nextProps.card_name,
        })
    }

    componentWillUpdate(nextProps, nextState){
        
    }

    componentDidUpdate(){

    }

    componentDidMount(){
        
    }

    get_add_form(data){
        let form_data = this.props.controller.get_line_form(data, this.state.metadata, this.state.quotation_id)

        form_data.onSubmit = this.handle_add_form_submit
        form_data.onSubmited = () => this.dismiss_modal_form()
        form_data.onCancel = () => this.dismiss_modal_form()

        return form_data
    }

    get_edit_form(data){
        let form_data = this.props.controller.get_line_form(data, this.state.metadata)

        form_data.onSubmit = this.handle_edit_form_submit
        form_data.onSubmited = () => this.dismiss_modal_form()
        form_data.onCancel = () => this.dismiss_modal_form()

        return form_data
    }

    handle_add_click = () => {
        this.setState({ is_show_modal_form: true, data_modal_form: this.get_add_form({ reset: true }) })
    }

    handle_edit_click = (data) => {
        this.setState({ is_show_modal_form: true, data_modal_form: this.get_edit_form(data) })
    }

    handle_delete_click = (data) => {
        this.props.controller.line_delete(data, () => {
            //succès
        }, () => {
            //error
        })
    }

    handle_add_form_submit = (data, success, error) => {
        this.props.controller.line_create(data, () => {
            this.refresh()
            success()
        }, error)
    }

    handle_edit_form_submit = (data, success, error) => {
        this.props.controller.line_update(data, () => {
            this.refresh()
            success()
        }, error)
    }

    dismiss_modal_form(){
        this.setState({ is_show_modal_form: false })
    }

    
    should_render(data, item){

        if (item.type === 'hidden') {

            switch(item.extra){
                case 'button_edit':
                    return this.state.quotation_data.is_editable

                case 'button_delete':
                    return this.state.quotation_data.is_deletable
            }

        }

        return true
    }

    render_column(data, item){
        if (item.type === 'hidden') {
            switch(item.extra){
                case 'evaluation':
                    let str = decodeURIComponent(item.rule)
                    str = Mustache.render(str, data)
                    // console.log(str, eval(str))
                    return eval(str)
                    break;

                case 'button_edit':
                    return (
                        <ButtonEdit 
                            title={this.props.lang.line('std_edit')}
                            onClick={ (e) => this.handle_edit_click(data, e) }
                             />
                    )
                    break;

                case 'button_delete':
                    return (
                        <ButtonTooltip 
                            className="btn-danger"
                            icon_class_name="fa fa-trash"
                            title={this.props.lang.line('std_confirmation')}
                            message={this.props.lang.line('std_realy_delete_q')}
                            onOkClick={(e) => this.handle_delete_click(data, e)}

                             />
                    )
                    break;
            }

            return (<div></div>)
        }

        return data[item.name]
    }

    refresh(){
        this.forceUpdate()
    }

    render_row(data, index){

        return (
            <tr key={index}>
                {
                    this.state.metadata.data_list.map((item, index) => (
                        this.should_render(data, item) &&
                        <td className={ item.class }>
                            { 
                                (item.type === 'numeric' || item.extra === 'evaluation') ?
                                StringHelper.number_format(this.render_column(data, item), 2, ',', ' ') :
                                this.render_column(data, item)
                            }
                        </td>
                    ))
                }
            </tr>
        )
    }

    render() {

        const { 
            quotation_id,
            quotation_data,
            is_show_modal_form,
            data_modal_form,
            metadata,
            card_name
        } = this.state

        const data_list = this.props.controller.get_line_list(quotation_id)
        const resume = this.props.controller.get_line_resume(quotation_data)
        let price_total_index = 0
        if (metadata !== undefined) {
            price_total_index = this.props.controller.get_price_unit_index(metadata.data_list)
        }

        return(
            <div className={"card-table "}>
                

                <table className="table table-striped">
                    <thead className="thead-dark">
                        <tr>
                            {
                                metadata.data_list.map((item, index) => (
                                    this.should_render(quotation_data, item) &&
                                    <th className={item.className + ' '}  scope="col" key={index}>
                                        {item.label}
                                    </th>
                                ))
                            }
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data_list.map((data, index) => (
                                this.render_row(data)
                            ))
                        }

                        {
                            quotation_data.is_editable &&
                            <tr>
                                <td>
                                    <button className="btn btn-default" onClick={this.handle_add_click}>
                                        <i className="fa fa-plus"></i>

                                        { this.props.lang.line('std_add') }
                                    </button>
                                </td>
                            </tr>
                        }

                        { /*Price total*/ }
                        <tr className="resume">
                            <td colspan={price_total_index - 1}>
                                
                            </td>
                            <td className="resume">
                                { this.props.lang.line('std_price_total_ht') }
                            </td>
                            <td className="text-right">
                                { StringHelper.number_format(resume.prix_total_ht, 2, ',', ' ') }
                            </td>
                        </tr>

                        { /*Reduction*/ }
                        {
                            (quotation_data.reduction !== '' && quotation_data.reduction != 0) &&
                            <tr>
                                <td colspan={price_total_index - 1}>
                                    
                                </td>
                                <td className="resume">
                                    { this.props.lang.line('std_reduction') }
                                </td>
                                <td className="text-right">
                                    { StringHelper.number_format(quotation_data.reduction, 2, ',', ' ') }
                                </td>
                            </tr>
                        }
                        

                        { /*Price Reducted*/ }
                        {
                            (quotation_data.reduction !== '' && quotation_data.reduction != 0) &&
                            <tr>
                                <td colspan={price_total_index - 1}>
                                    
                                </td>
                                <td className="resume">
                                    { this.props.lang.line('std_price_reducted') }
                                </td>
                                <td className="text-right">
                                    { StringHelper.number_format(resume.prix_total_remise, 2, ',', ' ') }
                                </td>
                            </tr>
                        }

                        { /*TVA*/ }
                        <tr>
                            <td colspan={price_total_index - 1}>
                                
                            </td>
                            <td className="resume">
                                { this.props.lang.line('std_tva') }
                            </td>
                            <td className="text-right">
                                { StringHelper.number_format(resume.prix_total_tva, 0, ',', ' ') } %
                            </td>
                        </tr>

                        { /*Total TTC*/ }
                        <tr>
                            <td colspan={price_total_index - 1}>
                                
                            </td>
                            <td className="resume">
                                { this.props.lang.line('std_price_ttc') }
                            </td>
                            <td className="text-right">
                                { StringHelper.number_format(resume.prix_ttc, 2, ',', ' ') }
                            </td>
                        </tr>

                        { /*Total TTC*/ }
                        <tr>
                            <td colspan={price_total_index + 1} className="resume-litteral">
                                <p><b>{ StringHelper.sprintf(this.props.lang.line('std_x_price_stoped_at'), this.props.lang.line('std_' + card_name + '_lower')) }</b> : { StringHelper.uc_first(resume.prix_ttc_litteral) }</p>
                            </td>
                        </tr>

                    </tbody>
                </table>

                {
                    is_show_modal_form &&
                    <ModalForm
                        ajax={this.props.ajax}
                        lang={this.props.lang}
                        data={data_modal_form}
                        />
                }
            </div>
        )
    }
}

export default Table


