class Entity {
    ajax = null

    list = []
    metadata_list = {}
    metadata = null
    index = 0
    nb = 10
    is_last = false
    list_change_callback_list = []
    data_change_callback_list = []
    header_list = []
    sort_list = null

    data = null
    entity = ''
    form = null

    constructor(props){
        this.ajax = props.ajax
        this.lang = props.lang

        this.refresh_callback = props.refresh_callback

        this.props = props
        this.entity = props.entity

        // this.props.hub.add_event_listner(this.handle_hub_change)
        //todo : hub event listener for list_change, data_change
    }

    get_entity(){
        return this.entity
    }

    set_entity(_value){
        this.entity = _value
        this.list = []
        this.index = 0
        this.metadata = null
        this.form = null
        this.dispatch_list_change()
    }

//List
    is_sending_request = false
    get_list({key, order_by, nb, sort}, is_getting_more, callback){

        if (is_getting_more && !this.is_sending_request) {

            if (nb !== undefined) {
                this.nb = nb
            }

            this.is_sending_request = true

            this.props.ajax.send_request({
                url: 'entity',
                type: 'GET',
                data: { key, order_by, sort, nb: this.nb, debut: this.index, entity: this.entity },
                data_type: 'json',
                // before_send: () => { before_send() },
                done: (xhr, response) => {

                    this.is_sending_request = false

                    if (xhr.status === 200) {
                        // this.index = this.index + this.nb

                        if (response.data_list.length === 0) {
                            this.is_last = true
                        }
                        else{

                            this.index = this.index + this.nb
                            this.count = response.count
                            this.header_list = response.header_list
                            response.data_list.map((data) => {
                                this.list.push(data)
                            })
                        }
                        
                        this.dispatch_list_change()
                    }

                    if (xhr.status === 500 || xhr.status === 400) {
                        // this.refresh('internal_error')
                    }

                }
            })
        }

        return this.list
    }

    get_header_list(){
        return this.header_list
    }

    clear_list(){
        this.index = 0
        this.is_last = false
        this.list = []
    }

    is_last(){
        return this.is_last
    }

    //Event_listener
    add_event_listner(event_name, _function){
        if (event_name === undefined) {
            return false
        }

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.push(_function);
                break;

            case 'data_change':
                this.data_change_callback_list.push(_function);
                break;

        }
    }

    remove_event_listner = (event_name, _function) => {

        if (event_name === undefined) {
            return false
        }

        var index_catched = null

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.list_change_callback_list.splice(index_catched, 1)
                break;

            case 'data_change':
                this.data_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.data_change_callback_list.splice(index_catched, 1)
                break;
        }
    }

    dispatch_list_change(param){
        this.list_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }

    dispatch_data_change(param){
        this.data_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }

    get_select_list(callback, is_getting_more){

        this.get_list({ order_by: 'designation', sort: 'asc', nb: 100 }, is_getting_more === undefined, (data_list) => {
            let result = []
            data_list.map((data, index) => {
                if (data.id != 2) {
                    result.push({
                        value: data.id,
                        label: data.designation,
                        commentaire: data.commentaire,
                    })
                }
            })

            callback(result)
        })

    }

    get_sort_data_list(callback){

        if (this.sort_list === null) {
            this.props.ajax.send_request({
                url: 'sort/entity',
                type: 'GET',
                data: { entity: this.entity },
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    this.sort_list = response
                    callback(response)

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.sort_list
    }

    create(data, success, error){

        data.entity = this.get_entity()

        this.props.ajax.send_request({
            url: 'entity',
            type: 'POST',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.dispatch_list_change()
                    success(response)
                }

                if (xhr.status === 500 || xhr.status === 400) {
                    let message = ''
                    for(let index in response.messages){
                        message = response.messages[index]
                    }
                    error(message)
                }
            }
        })
    }

    read(id){
        if (this.metadata === null) {
            this.props.ajax.send_request({
                url: 'entity/' + id,
                type: 'GET',
                data: { entity: this.get_entity() },
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    if (xhr.status === 200) {
                        this.metadata = response
                        this.dispatch_data_change(response)
                    }

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.metadata
    }

    update(data, success, error){

        //update_list
        for(let index in data){
            this.metadata[index] = data[index]
        }

        data.entity = this.get_entity()

        this.props.ajax.send_request({
            url: 'entity',
            type: 'PUT',
            data: data,
            content_type: 'json',
            data_type: 'json',
            // before_send: () => { before_send() },
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.props.ajax.set_cache('entity/'+ this.metadata.id, 'GET', this.metadata)
                    success()
                }

                // if (xhr.status === 500 || xhr.status === 400) {
                //     error(response.messages.error)
                // }
            }
        })
    }

    delete(id, success, error){

        this.props.ajax.send_request({
            url: 'entity',
            type: 'DELETE',
            data: { id: id, entity: this.get_entity() },
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 204) {
                    //update_metadata
                    this.metadata = undefined
                    return success()
                }

                return error()
            }
        })
    }

    access(callback){

        this.props.ajax.send_request({
            url: 'access/entity',
            type: 'GET',
            data: { entity: this.entity },
            data_type: 'json',
            cache: true,
            done: (xhr, response) => {

                if (xhr.status === 200) {
                    callback(response)
                }

            }
        })
    }

    get_btn_cmd(data_structure){
        if (data_structure === null) {
            return []
        }
        
        let result = []
        data_structure.data_list.map((data) => {
            if (data.type === 'hidden' && data.extra === 'command') {
                result = data.data_list
            }
        })

        return result
    }

    get_form(data, callback){

        if (this.form === null) {
            this.props.ajax.send_request({
                url: 'structure/entity',
                type: 'GET',
                data: { entity: this.entity },
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    if (xhr.status === 200) {
                        this.form = response
                        this.form.title = this.props.lang.line('std_' + this.get_entity())
                        
                        if (callback !== undefined) {
                            callback()
                        }
                    }

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        if (this.form !== null) {
            let has_id = false

            this.form.data_list.map((_data) => {
                for(let index in data){

                    if (_data.name === 'id') {
                        has_id = true
                    }

                    if (_data.name === index) {
                        _data.value = data[index]
                    }
                }

                if (data.reset) {
                    _data.value = undefined
                }
            })



            if (!has_id && data.id !== undefined) {
                this.form.data_list.push({
                    type: 'hidden',
                    name: 'id',
                    value: data.id,
                })
            }
        }

        return this.form
    }

}


export default Entity


