import React, { Component } from 'react'

import { Spinner } from 'mzara-component'
import { User as UserController } from 'mzara-controller'
import ButtonSpinner from '../../component/btn/ButtonSpinner'

import { FormControl, Alert } from 'mzara-component'

class Login extends Component {
    state = {

    }

    form_list = []

    constructor(props){

        super(props)

        this.state = {
            is_submiting: false,
            login_err: '',
        }

        if (props.user_controller === undefined) {
            this.init_user_controller()
        }else{
            this.user_controller = props.user_controller
        }
    }

    componentWillMount(){

    }

    componentWillUnmount(){

    }

    componentWillUpdate(nextProps, nextState){

    }

    componentDidMount(){

    }

    get_form_ref(ref, unmount){
        if (unmount) {
            var form_index = null
            this.form_list.map((data, index) => {
                if (ref.state.input_name === data.state.input_name) {
                    form_index = index
                }
            })
            
            if (form_index !== null) {
                this.form_list.splice(form_index, 1)
            }
            
        }else{
           this.form_list.push(ref) 
        }
        
    }

    init_user_controller(){
        this.user_controller = new UserController({
            ajax: this.props.ajax,
            hub: this.props.hub,
            lang: this.props.lang,

            refresh_callback: (e, data) => this.refresh(e, data),
        })
    }

    set_on_submit(){
        this.setState({ is_submiting: true })
    }

    set_submited(){
        this.setState({ is_submiting: false })
    }

    handle_login_form_submit = (e) => {
        e.preventDefault()
        if (!this.state.is_submiting) {
            var form_serialized = FormControl.serialize_form(this.form_list)

            if (form_serialized.should_submit) {

                this.user_controller.login({
                    data: form_serialized.result,
                    success: () => { this.props.onSubmited() },
                    error: (error_message) => { this.setState({ login_err: error_message }) },
                    before_send: () => { this.set_on_submit() },
                    request_sent: () => { this.set_submited() }
                })
                
            }
        }
    }

    handle_login_value_change = (value) => {
        this.login_value = value
    }

    render() {

        const { is_submiting, login_err } = this.state

        return (
            <div className="form-container animated fadeIn">

                <div>
                    <p className="text-center"><img src={this.props.ajax.site_url('assets/img/logo.png')} style={{ maxHeight: '128px' }} /></p>

                    {
                        login_err.length > 0 &&
                        <Alert 
                            is_dissmissable={true}
                            alert_type="danger"
                            text={login_err}
                            />
                    }

                    <div>
                        <form action={this.props.ajax.site_url('user/login')} methode="POST" onSubmit={this.handle_login_form_submit}>
                            <FormControl 
                                lang={this.props.lang}
                                label={this.props.lang.line('std_login_info')}
                                input_type="text"
                                input_name="user_name"
                                input_id="user_name"
                                input_value={this.login_value}
                                input_placeholder={this.props.lang.line('std_email_or_phone_number')}
                                is_required={true}
                                is_email_valid={true}
                                onRef={ (ref, unmount) => this.get_form_ref(ref, unmount) }
                                onChange={this.handle_login_value_change}
                                />

                            <FormControl 
                                lang={this.props.lang}
                                label={this.props.lang.line('std_password')}
                                input_type="password"
                                input_name="password"
                                input_id="password"
                                input_placeholder={this.props.lang.line('std_password')}
                                is_required={true}
                                ref={this.password_input}
                                onRef={ (ref, unmount) => this.get_form_ref(ref, unmount) }
                                />

                            <p className="text-right text-small mb-15"><a href={ this.props.ajax.site_url('user/forgot_pass') }>{this.props.lang.line('std_forgot_password_q')}</a></p>

                            <button className={"btn btn-primary btn-block mb-15 " + (is_submiting ? 'disabled' : '')} type="submit">
                                {
                                    is_submiting &&
                                    <ButtonSpinner />
                                }

                                { this.props.lang.line('std_login') }
                            </button>
                        

                            <p className="text-center text-small text-gray mb-15">{this.props.lang.line('std_or')}</p>

                            <a className="btn btn-success btn-block mb-30" href={this.props.ajax.site_url('user/sign_up')}>
                                { this.props.lang.line('std_signup') }
                            </a>

                        </form>
                    </div>
                </div>

            </div>
        )
    }
}

export default Login


