import React, { Component } from 'react'

import { FormControl, Dropdown, Beabcrumb, ModalForm, Gallery, Spinner } from 'mzara-component'
import { DOMHelper, Utils } from 'mzara-library'
import Mustache from 'mustache'

import LinkBack from '../../component/btn/LinkBack'
import ButtonEdit from '../../component/btn/ButtonEdit'
import ButtonTooltip from '../../component/btn/ButtonTooltip'

class Read extends Component {
    state = {

    }

    form_list = []

    constructor(props){
        super(props)

        this.state = {
            is_load: !props.is_self_trigger,
            id: props.id,
            data: props.data,
            card_name: props.card_name,
            is_self_trigger: props.is_self_trigger,
            is_show_modal_form: false,
        }

        this.controller = props.controller
        this.controller.add_event_listner('data_change', this.handle_data_change)
    }

    componentWillMount(){

    }

    componentWillUnmount(){
        this.controller.remove_event_listner('data_change', this.handle_data_change)
    }

    componentWillReceiveProps(nextProps){
        this.init_controller()

        this.setState({ 
            data: nextProps.data, 
            layout: nextProps.layout,
            card_name: nextProps.card_name,
        })

    }

    componentWillUpdate(nextProps, nextState){
        
    }

    componentDidUpdate(){

    }

    componentDidMount(){

    }

    get_form_ref(ref, unmount){
        if (unmount) {
            var form_index = null
            this.form_list.map((data, index) => {
                if (ref.state.input_name === data.state.input_name) {
                    form_index = index
                }
            })
            
            if (form_index !== null) {
                this.form_list.splice(form_index, 1)
            }
            
        }else{
           this.form_list.push(ref) 
        }
        
    }

    get_beabcrumb_data(data){
        let result = []

        //Home
        result.push({
            label: this.props.lang.line('std_home'),
            href: this.props.ajax.site_url(''),
        })

        //Account
        result.push({
            label: this.props.lang.line('std_product'),
            href: this.props.ajax.site_url('product/'),
        })

        if (data !== undefined && data !== null) {
            //Nom actuelle
            result.push({
                label: data.designation,
            })
        }

        return result
    }

    get_edit_form(form_data){
        form_data.onSubmit = this.handle_edit_form_submit
        form_data.onSubmited = () => this.dismiss_modal_form()
        form_data.onCancel = () => this.dismiss_modal_form()

        return form_data
    }

    handle_edit_click = (data, e) => {

        let data_structure = this.props.controller.get_form(Utils.get_clone(data), () => this.handle_edit_click(data, e))

        if (data_structure !== null) {
            this.setState({ is_show_modal_form: true, data_modal_form: this.get_edit_form(data_structure) })
        }

        
    }

    handle_edit_form_submit = (data, success, error) => {
        this.controller.update(data, () => {
            success()
        }, error)
    }

    handle_delete_click = (data, e) => {
        this.controller.delete(data.id, () => {

            this.props.navigation.parent.show_message({
                type: 'success',
                text: this.props.lang.line('std_deleted_successfully'),
                duration: 7000,
            })

            this.props.navigation.load('product')
        }, (error_message) => {
            this.props.navigation.parent.show_message({
                text: error_message,
                type: 'danger',
            })
        })
    }

    handle_link_click = (href, target, e) => {
        if (e !== undefined) {
            e.preventDefault()
        }

        if (target) {
            return window.open(href, '_blank')
        }

        return this.props.navigation.load(href)
    }

    handle_data_change = (data) => {
        if (this.state.is_self_trigger) {

            this.setState({ is_load: true })
        }else{
            this.setState({ data: data })
        }
        
    }

    dismiss_modal_form(){
        this.setState({ is_show_modal_form: false })
    }

    get_href_param(url, data){

        if (url === null || url === undefined) {
            return ''
        }

        url = decodeURIComponent(url)

        return Mustache.render(url, data)
    }

    get_data_view(structure, data){
        if (structure.view !== undefined) {
            return data[structure.view]
        }

        return data[structure.name]
    }

    should_render_btn(btn, data){
        if (btn.condition === undefined) {
            return true
        }

        let str_eval = Mustache.render(btn.condition, data)

        return eval(str_eval)
    }

    refresh(){
        this.forceUpdate()
    }

    render_btn(btn, data, index){
        if (btn.type === 'edit') {
            return (
                <ButtonEdit 
                    key={index}
                    text={this.props.lang.line('std_edit')}
                    onClick={ (e) => this.handle_edit_click(data, e) }
                     />
            )
        }

        if (btn.type === 'delete') {
            return (
                <ButtonTooltip 
                    key={index}
                    className="btn-danger"
                    icon_class_name="fa fa-trash"
                    text={this.props.lang.line('std_delete')}
                    title={this.props.lang.line('std_confirmation')}
                    message={this.props.lang.line('std_realy_delete_q')}
                    onOkClick={(e) => this.handle_delete_click(data, e)}
                     />
            )
        }

        if (btn.type === 'link') {

            if (btn.extra === 'confirmation') {
                return (
                    <ButtonTooltip 
                        key={index}
                        className="btn-info"
                        icon_class_name="fa fa-check"
                        text={this.props.lang.line('std_validate')}
                        title={this.props.lang.line('std_confirmation')}
                        message={this.props.lang.line('std_really_wanna_validate_q')}
                        onOkClick={(e) => this.handle_link_click(btn.href, btn.target_blank, e)}
                         />
                )
            }

            return (

                <a 
                    key={index}
                    href={btn.href} 
                    className={btn.class} 
                    onClick={(e) => this.handle_link_click(btn.href, btn.target_blank, e)}>
                    <i className={ btn.icon }></i>
                    { btn.label }
                </a>
            )
            
        }
    }

    render_metadata_view(structure, data, index){

        return (
            <div className="metadata" key={index}>
                <label>{ structure.label } : </label>
                {
                    structure.url !== undefined &&
                    <a href={this.get_href_param(structure.url, data)}>{this.get_data_view(structure, data)}</a>
                }
                {
                    structure.url === undefined &&
                    <span>{this.get_data_view(structure, data)} </span>
                }
                
            </div>
        )
    }

    render() {

        const { 
            is_load,
            is_self_trigger,
            is_show_modal_form,
            data_modal_form,
        } = this.state

        let image_list = []
        let metadata = this.state.data
        let data_structure = {}

        if (is_self_trigger) {
            data_structure = this.props.controller.get_form(() => this.refresh())
            metadata = this.controller.read(this.state.id)
        }

        if (metadata !== null && metadata.image_list !== null && metadata.image_list !== undefined) {
            var adr = metadata.image_list.split(',')
            for (var i = 0; i < adr.length; i++) {
                image_list.push({
                    id: adr[i],
                    th_url: this.props.ajax.site_url('image/' + adr[i] + '?prefix=thx'), 
                    url: this.props.ajax.site_url('image/' + adr[i] + '?prefix=thm'),
                    delete_url: metadata.is_editable ? this.props.ajax.site_url('produit/delete_image/' + metadata.id + '/' + adr[i]) : undefined,
                })
            }
        }

        if (!is_load) {
            return (
                <div className="">
                    <Spinner position="center" />
                </div>
            )
        }

        return(

            <div className="card-wall">

                <div className="row">
                    <div className="col-12">
                        <div className="page-header">
                            <Beabcrumb
                                data_list={this.get_beabcrumb_data(metadata)}
                                />
                        </div>
                    </div>
                </div>

                <div className="row mt-30">
                    <div className="col-12 col-md-8">
                        <div className="box box-white mb-20 card-metadata product-metadata p-15">

                            <h1>{metadata.name}</h1>

                            <div className="row">
                                <div className="col-12 col-md-4">
                                    <div>
                                        <Gallery
                                            lang={this.props.lang}
                                            ajax={this.props.ajax}
                                            navigation={this.navigation}
                                            is_auto_slide={true}
                                            is_autosize={true}
                                            image_list={image_list}  
                                            onImageClick={this.handle_image_click}
                                            ref={ this.gallery_component }
                                            />
                                    </div>
                                </div>

                                <div className="col-12 col-md-8">
                                    <div>
                                        <div className="metadata">
                                            <label>{ this.props.lang.line('std_name') } : </label>

                                            <span>{ metadata.name } </span>
                                        </div>

                                        <div className="metadata">
                                            <label>{ this.props.lang.line('std_category') } : </label>

                                            <span>{ metadata.product_category } </span>
                                        </div>

                                        <div className="metadata">
                                            <label>{ this.props.lang.line('std_price_before') } : </label>

                                            <span>{ metadata.price_before } </span>
                                        </div>

                                        <div className="metadata">
                                            <label>{ this.props.lang.line('std_price') } : </label>

                                            <span>{ metadata.price } </span>
                                        </div>

                                        
                                    </div>
                                </div>
                            </div>

                            <div className="card-footer">
                                <div className="">
                                    <LinkBack href={this.props.ajax.site_url('product')} lang={this.props.lang} />
                                </div>
                                <div className="ml-auto d-flex">

                                </div>
                            </div>

                        </div>
                    </div>
                </div>

                {
                    is_show_modal_form &&
                    <ModalForm
                        ajax={this.props.ajax}
                        lang={this.props.lang}
                        data={data_modal_form}
                        />
                }
            </div>
        )
    }
}

export default Read


