import React, { Component } from 'react'

import { Dropdown } from 'mzara-component'
import { DOMHelper } from 'mzara-library'
import Mustache from 'mustache'

class ListItem extends Component {
    state = {

    }

    lang = null
    navigation = null
    gallery_component = null

    constructor(props){
        super(props)

        this.state = {
            is_load: !props.is_self_trigger,
            id: props.id,
            data: props.data,
            header_list: props.header_list,
            is_self_trigger: props.is_self_trigger,
            layout: props.layout,
            onClick: props.onClick,
        }

        if (props.is_self_trigger) {
            this.controller = props.controller
        }
    }

    componentWillMount(){

    }

    componentWillUnmount(){

    }

    componentWillReceiveProps(nextProps){
        this.setState({ data: nextProps.data, layout: nextProps.layout, header_list: nextProps.header_list })
    }

    componentWillUpdate(nextProps, nextState){
        
    }

    componentDidUpdate(){

    }

    componentDidMount(){
        if (this.state.is_self_trigger) {
            this.controller.get_metadata((data) => {
                this.setState({ data: data, is_load: true })
            })
            // this.get_metadata(this.state.tag_link)
        }
    }

    handle_item_click = (e) => {
        e.preventDefault()

        if (!DOMHelper.hasClass(e.target, 'btn') && this.state.onClick !== undefined) {
            this.state.onClick(this.state.data, this, e)
        }
    }

    refresh(){
        this.forceUpdate()
    }

    render_item(header, data, index){
        if (header.url !== undefined) {

            data.entity = this.props.controller.get_entity()
            header.url = decodeURIComponent(header.url)
            let url = Mustache.render(header.url, data)

            return (
                <a href={ url }>{ data[header.index] }</a>
            )
        }

        return data[header.index]
    }

    render() {

        const { 
            is_load,
            header_list,
            data,
            layout,
            is_submiting,
            is_simple,
        } = this.state

        return(
            <div 
                className={"fiche-item"}
                onClick={this.handle_item_click}>
                <div className="row">
                    {
                        header_list.map((header, index) => (
                            <div className={header.class}>
                                { this.render_item(header, data, index) }
                            </div>
                        ))
                    }
                </div>

                {
                    (header_list.menu !== undefined && header_list.menu.length > 0) &&
                    <Dropdown
                        position="top right"
                        dropdown_btn={( 
                            <button className="btn btn-menu" type="button">
                                <i className="fa fa-ellipsis-v"></i> 
                            </button> 
                        )}
                        dropdown_element={
                            (
                                <div className="dropdown-menu show">
                                    {
                                        header_list.menu.map((_menu, index) => (
                                            <a className={"dropdown-item " + _menu.class} href={ _menu.url } key={index}> { _menu.value }</a>
                                        ))
                                    }
                                </div>
                            )
                        }
                        /> 
                }

            </div>
        )
    }
}

export default ListItem


