import UserRole from '../user_role'

class Account {
    ajax = null

    list = []
    metadata_list = {}
    metadata = null
    index = 0
    nb = 10
    is_last = false
    list_change_callback_list = []
    data_change_callback_list = []
    sort_list = null

    data = null

    constructor(props){
        this.ajax = props.ajax
        this.lang = props.lang

        this.refresh_callback = props.refresh_callback

        this.props = props

        // this.props.hub.add_event_listner(this.handle_hub_change)
        //todo : hub event listener for list_change, data_change
    }

//List
    is_sending_request = false
    get_list({key, order_by, sort, user_category_id}, is_getting_more){

        if (is_getting_more && !this.is_sending_request) {

            this.is_sending_request = true

            this.props.ajax.send_request({
                url: 'account',
                type: 'GET',
                data: { key, order_by, sort, nb: this.nb, debut: this.index, user_category_id: user_category_id },
                data_type: 'json',
                // before_send: () => { before_send() },
                done: (xhr, response) => {

                    this.is_sending_request = false

                    if (xhr.status === 200) {
                        this.index = this.index + this.nb

                        if (response.length === 0) {
                            this.is_last = true
                        }
                        else{
                            response.map((data) => {
                                this.list.push(data)
                            })
                        }
                        
                        this.dispatch_list_change()
                    }

                    if (xhr.status === 500 || xhr.status === 400) {
                        // this.refresh('internal_error')
                    }                    
                }
            })
        }

        return this.get_account_category(user_category_id)
    }

    get_account_category(user_category_id){

        let result = []

        this.list.map((account, index) => {

            if (user_category_id === null && account.user_category_id === null) {
                result.push(account)
            }
            else if (account.user_category_id == user_category_id) {
                result.push(account)
            }
        })

        return result
    }

    clear_list(){
        this.index = 0
        this.is_last = false
        this.list = []
    }

    is_last(){
        return this.is_last
    }

    get_sort_data_list(callback){

        if (this.sort_list === null) {
            this.props.ajax.send_request({
                url: 'sort/account',
                type: 'GET',
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    this.sort_list = response
                    callback(response)

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.sort_list
    }

    create(data, success, error){
        this.props.ajax.send_request({
            url: 'account',
            type: 'POST',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.dispatch_list_change()
                    success(response)
                }

                if (xhr.status === 500 || xhr.status === 400) {
                    let message = ''
                    for(let index in response.messages){
                        message = response.messages[index]
                    }
                    error(message)
                }
            }
        })
    }

    create_client(data, success, error){
        this.props.ajax.send_request({
            url: 'account/client',
            type: 'POST',
            data: data,
            content_type: 'json',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.dispatch_list_change()
                    success(response)
                }

                if (xhr.status === 500 || xhr.status === 400) {
                    let message = ''
                    for(let index in response.messages){
                        message = response.messages[index]
                    }
                    error(message)
                }
            }
        })
    }

    update(data, success, error){
        //update_list
        for(let index in data){
            this.metadata[index] = data[index]
        }

        this.props.ajax.send_request({
            url: 'account',
            type: 'PUT',
            data: data,
            content_type: 'json',
            data_type: 'json',
            // before_send: () => { before_send() },
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.props.ajax.set_cache('account/'+ this.metadata.id, 'GET', this.metadata)
                    success()
                }

                // if (xhr.status === 500 || xhr.status === 400) {
                //     error(response.messages.error)
                // }
            }
        })
    }

    read(id){
        if (this.metadata === null) {
            this.props.ajax.send_request({
                url: 'account/' + id,
                type: 'GET',
                data_type: 'json',
                cache: true,
                done: (xhr, response) => {

                    if (xhr.status === 200) {
                        this.metadata = response
                        this.dispatch_data_change(response)
                    }

                    // if (xhr.status === 500 || xhr.status === 400) {
                    //     error(response.messages.error)
                    // }
                }
            })
        }

        return this.metadata
    }

    delete(id){

        this.props.ajax.send_request({
            url: 'account',
            type: 'DELETE',
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    let index_catched = null
                    this.list.map((account, index) => {
                        if (account.id == id) {
                            index_catched = index
                        }
                    })

                    if (index_catched !== null) {
                        this.list.splice(index_catched, 1)
                        this.dispatch_list_change()
                    }

                    //update_metadata
                    this.metadata_list['_' + id] = undefined
                    this.dispatch_data_change(this.metadata_list['_' + id])
                }

                // if (xhr.status === 500 || xhr.status === 400) {
                //     error(response.messages.error)
                // }
            }
        })
    }

    block(user_id, callback){
        this.props.ajax.send_request({
            url: 'user/block',
            type: 'POST',
            data: { user_id: user_id },
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.metadata.is_blocked = 1
                    this.metadata.can_block = !this.metadata.can_block
                    this.metadata.can_unblock = !this.metadata.can_block
                    this.props.ajax.set_cache('account/'+ this.metadata.id, 'GET', this.metadata)
                    return callback()
                }
            }
        })
    }

    unblock(user_id, callback){
        this.props.ajax.send_request({
            url: 'user/unblock',
            type: 'POST',
            data: { user_id: user_id },
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.metadata.is_blocked = 0
                    this.metadata.can_block = !this.metadata.can_block
                    this.metadata.can_unblock = !this.metadata.can_block
                    this.props.ajax.set_cache('account/'+ this.metadata.id, 'GET', this.metadata)
                    return callback()
                }
            }
        })
    }

    role(data, callback){
        this.props.ajax.send_request({
            url: 'user/role',
            type: 'POST',
            data: data,
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    this.metadata.user_role = response.user_role
                    this.metadata.user_role_id = response.user_role_id
                    this.props.ajax.set_cache('account/'+ this.metadata.id, 'GET', this.metadata)
                    return callback()
                }
            }
        })
    }

    password(data, success, error){
        this.props.ajax.send_request({
            url: 'user/update_password',
            type: 'POST',
            data: data,
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    return success()
                }

                return error(response.messages.error)
            }
        })
    }

    reset_password(data, success, error){
        this.props.ajax.send_request({
            url: 'user/reset_password',
            type: 'POST',
            data: data,
            data_type: 'json',
            done: (xhr, response) => {

                if (xhr.status === 201) {
                    return success()
                }

                return error(response.messages.error)
            }
        })
    }

    access(callback){

        this.props.ajax.send_request({
            url: 'access/account',
            type: 'GET',
            data_type: 'json',
            cache: true,
            done: (xhr, response) => {

                if (xhr.status === 200) {
                    callback(response)
                }

            }
        })
    }

    //Event_listener
    add_event_listner(event_name, _function){
        if (event_name === undefined) {
            return false
        }

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.push(_function);
                break;

            case 'data_change':
                this.data_change_callback_list.push(_function);
                break;

        }
    }

    remove_event_listner = (event_name, _function) => {

        if (event_name === undefined) {
            return false
        }

        var index_catched = null

        switch(event_name){

            case 'list_change':
                this.list_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.list_change_callback_list.splice(index_catched, 1)
                break;

            case 'data_change':
                this.data_change_callback_list.map((event, index) => {
                    if (event === _function) {
                        index_catched = index
                    }
                })

                if (index_catched === null){
                    return false
                }

                this.data_change_callback_list.splice(index_catched, 1)
                break;
        }
    }

    dispatch_list_change(param){
        this.list_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }

    dispatch_data_change(param){
        this.data_change_callback_list.map((event) => {
            if (typeof event === 'function') {
                event(param)
            }
        })
    }

    get_create_form(){
        return {
            title: this.props.lang.line('std_add_user'),
            data_list: [
                {
                    label: this.props.lang.line('std_name'),
                    type: 'text',
                    name: 'user_name',
                    id: 'user_name',
                    placeholder: this.props.lang.line('std_name'),
                    is_required: true,
                },
                {
                    label: this.props.lang.line('std_email'),
                    type: 'text',
                    name: 'email',
                    id: 'email',
                    placeholder: this.props.lang.line('std_email'),
                    is_required: true,
                },
                {
                    label: this.props.lang.line('std_password'),
                    type: 'password',
                    name: 'password',
                    id: 'password',
                    placeholder: this.props.lang.line('std_password'),
                    is_required: true,
                },

            ]

        }
    }

    get_create_client_form(user_category_id){
        return {
            title: this.props.lang.line('std_add_account_' + user_category_id),
            data_list: [
                {
                    type: 'hidden',
                    name: 'user_category_id',
                    value: user_category_id,
                },
                {
                    label: this.props.lang.line('std_name'),
                    type: 'text',
                    name: 'user_name',
                    id: 'user_name',
                    placeholder: this.props.lang.line('std_name'),
                    is_required: true,
                },
                {
                    label: this.props.lang.line('std_email'),
                    type: 'text',
                    name: 'email',
                    id: 'email',
                    placeholder: this.props.lang.line('std_email'),
                    is_required: true,
                },
                {
                    label: this.props.lang.line('std_phone'),
                    type: 'text',
                    name: 'telephone',
                    id: 'telephone',
                    placeholder: this.props.lang.line('std_phone'),
                },
                {
                    label: this.props.lang.line('std_adress'),
                    type: 'text',
                    name: 'adress',
                    id: 'adress',
                    placeholder: this.props.lang.line('std_adress'),
                },
                {
                    label: this.props.lang.line('std_website'),
                    type: 'text',
                    name: 'site_web',
                    id: 'site_web',
                    placeholder: this.props.lang.line('std_website'),
                },
                {
                    label: this.props.lang.line('std_nif'),
                    type: 'text',
                    name: 'nif',
                    id: 'nif',
                    placeholder: this.props.lang.line('std_nif'),
                },
                {
                    label: this.props.lang.line('std_stat'),
                    type: 'text',
                    name: 'stat',
                    id: 'stat',
                    placeholder: this.props.lang.line('std_stat'),
                },
                {
                    label: this.props.lang.line('std_rcs'),
                    type: 'text',
                    name: 'rcs',
                    id: 'rcs',
                    placeholder: this.props.lang.line('std_rcs'),
                },

            ]

        }
    }

    get_password_form(user_id){
        return {
            title: this.props.lang.line('std_change_password'),
            data_list: [
                {
                    type: 'hidden',
                    name: 'user_id',
                    id: 'user_id',
                    value: user_id,
                },
                {
                    label: this.props.lang.line('std_old_password'),
                    type: 'password',
                    name: 'old_password',
                    id: 'old_password',
                    placeholder: this.props.lang.line('std_old_password'),
                    is_required: true,
                },
                {
                    label: this.props.lang.line('std_new_password'),
                    type: 'password',
                    name: 'new_password',
                    id: 'new_password',
                    placeholder: this.props.lang.line('std_new_password'),
                    is_required: true,
                },
                {
                    label: this.props.lang.line('std_confirm_password'),
                    type: 'password',
                    name: 'confirm_new_password',
                    id: 'confirm_new_password',
                    placeholder: this.props.lang.line('std_confirm_password'),
                    is_required: true,
                }
            ]

        }
    }

    get_reset_password_form(user_id){
        return {
            title: this.props.lang.line('std_change_password'),
            data_list: [
                {
                    type: 'hidden',
                    name: 'user_id',
                    id: 'user_id',
                    value: user_id,
                },
                {
                    label: this.props.lang.line('std_new_password'),
                    type: 'password',
                    name: 'new_password',
                    id: 'new_password',
                    placeholder: this.props.lang.line('std_new_password'),
                    is_required: true,
                }
            ]

        }
    }

    get_role_form(user_id, user_role_id, callback){
        let user_role_controller = new UserRole({
            ajax: this.props.ajax,
            lang: this.props.lang
        })

        user_role_controller.get_select_list((user_role_list) => {

            let data = {
                title: this.props.lang.line('std_role'),
                data_list: [
                    {
                        type: 'hidden',
                        name: 'user_id',
                        id: 'user_id',
                        value: user_id,
                    },
                    {
                        label: this.props.lang.line('std_role'),
                        type: 'radio',
                        name: 'user_role_id',
                        id: 'user_role_id',
                        option: user_role_list,
                        value: user_role_id,
                        placeholder: this.props.lang.line('std_role'),
                        is_required: true,
                    }
                ]

            }

            callback(data)

        })
    }

    get_edit_form(data){
        let data_list = [
            {
                type: 'hidden',
                name: 'id',
                id: 'id',
                value: data.id,
            },
        ]

        data.view_metadata.map((metadata) => {
            data_list.push({
                label: this.props.lang.line('std_' + metadata),
                type: 'text',
                name: metadata,
                id: metadata,
                value: data[metadata],
                placeholder: this.props.lang.line('std_' + metadata),
            })
        })

        return {
            title: this.props.lang.line('std_change_password'),
            data_list: data_list
        }
    }

}


export default Account


