import React, { Component } from 'react'

import { Dropdown, ProfilePictureControl } from 'mzara-component'
import { DOMHelper } from 'mzara-library'

class ListItem extends Component {
    state = {

    }

    lang = null
    navigation = null
    gallery_component = null

    constructor(props){
        super(props)

        this.state = {
            is_load: !props.is_self_trigger,
            id: props.id,
            metadata: props.metadata,
            is_self_trigger: props.is_self_trigger,
            layout: props.layout,
            onClick: props.onClick,
        }

        if (props.is_self_trigger) {
            this.controller = props.controller
        }
    }

    componentWillMount(){

    }

    componentWillUnmount(){

    }

    componentWillReceiveProps(nextProps){
        this.setState({ metadata: nextProps.metadata, layout: nextProps.layout })
    }

    componentWillUpdate(nextProps, nextState){
        
    }

    componentDidUpdate(){

    }

    componentDidMount(){
        if (this.state.is_self_trigger) {
            this.controller.get_metametadata((metadata) => {
                this.setState({ metadata: metadata, is_load: true })
            })
            // this.get_metametadata(this.state.tag_link)
        }
    }

    handle_item_click = (e) => {
        e.preventDefault()

        if (!DOMHelper.hasClass(e.target, 'btn') && this.state.onClick !== undefined) {
            this.state.onClick(this.state.metadata, this, e)
        }
    }

    refresh(){
        this.forceUpdate()
    }

    render_item(_data){

        if (_data.url !== undefined) {
            return (
                <a href={_data.url}>{_data.value}</a>
            )
        }

        return _data.value
    }

    render() {

        const { 
            is_load,
            metadata,
            layout,
            is_submiting,
            is_simple,
        } = this.state

        //Todo : is_load status

        return(
            <div 
                className={"fiche-item card-item"}
                onClick={this.handle_item_click}>



                <div className="row">

                    {
                        metadata.data.map((_data, index) => (
                            <div className={_data.class} key={index}>
                                <div>
                                    <h3>{ this.render_item(_data) }</h3>
                                </div>
                            </div>
                        ))
                    }
                </div>

                {
                    (metadata.menu !== undefined && metadata.menu.length > 0) &&
                    <Dropdown
                        position="top right"
                        dropdown_btn={( 
                            <button className="btn btn-menu" type="button">
                                <i className="fa fa-ellipsis-v"></i> 
                            </button> 
                        )}
                        dropdown_element={
                            (
                                <div className="dropdown-menu show">
                                    {
                                        metadata.menu.map((_menu, index) => (
                                            <a className={"dropdown-item " + _menu.class} href={ _menu.url } key={index}> { _menu.value }</a>
                                        ))
                                    }
                                </div>
                            )
                        }
                        /> 
                }

                
            </div>
        )
    }
}

export default ListItem


