import React, { Component } from 'react'

import { FormControl, Dropdown, Beabcrumb, ModalForm, ProfilePictureControl, Spinner } from 'mzara-component'
import { DOMHelper } from 'mzara-library'
import { Account as AccountController } from 'mzara-controller'

import ButtonTooltip from '../../component/btn/ButtonTooltip'
import ButtonBlock from '../../component/btn/ButtonBlock'
import ButtonEdit from '../../component/btn/ButtonEdit'

class Read extends Component {
    state = {

    }

    form_list = []

    constructor(props){
        super(props)

        this.state = {
            is_load: !props.is_self_trigger,
            id: props.id,
            data: props.data,
            is_self_trigger: props.is_self_trigger,
            is_show_modal_form: false,
        }

        this.init_controller()
    }

    componentWillMount(){

    }

    componentWillUnmount(){
        this.controller.remove_event_listner('data_change', this.handle_data_change)
    }

    componentWillReceiveProps(nextProps){
        this.init_controller()
        this.setState({ data: nextProps.data, layout: nextProps.layout })

    }

    componentWillUpdate(nextProps, nextState){
        
    }

    componentDidUpdate(){

    }

    componentDidMount(){

    }

    init_controller(){
        this.controller = new AccountController({
            ajax: this.props.ajax,
            lang: this.props.lang,
            hub: this.props.hub,

            refresh_callback: (e, data) => this.refresh(e, data),
        })

        this.controller.add_event_listner('data_change', this.handle_data_change)
    }

    get_form_ref(ref, unmount){
        if (unmount) {
            var form_index = null
            this.form_list.map((data, index) => {
                if (ref.state.input_name === data.state.input_name) {
                    form_index = index
                }
            })
            
            if (form_index !== null) {
                this.form_list.splice(form_index, 1)
            }
            
        }else{
           this.form_list.push(ref) 
        }
        
    }

    get_beabcrumb_data(data){
        let result = []

        //Home
        result.push({
            label: this.props.lang.line('std_home'),
            href: this.props.ajax.site_url(''),
        })

        //Account
        result.push({
            label: this.props.lang.line('std_account'),
            href: this.props.ajax.site_url('account'),
        })

        if (data !== undefined) {
            //Nom actuelle
            result.push({
                label: data.view_name,
            })
        }

        return result
    }

    get_password_form(user_id){
        let form_data = this.controller.get_password_form(user_id)
        form_data.onSubmit = this.handle_password_form_submit
        form_data.onSubmited = () => this.dismiss_modal_form()
        form_data.onCancel = () => this.dismiss_modal_form()

        return form_data
    }

    get_reset_password_form(user_id){
        let form_data = this.controller.get_reset_password_form(user_id)
        form_data.onSubmit = this.handle_reset_password_form_submit
        form_data.onSubmited = () => this.dismiss_modal_form()
        form_data.onCancel = () => this.dismiss_modal_form()

        return form_data
    }

    get_edit_form(user_id){
        let form_data = this.controller.get_edit_form(user_id)
        form_data.onSubmit = this.handle_edit_form_submit
        form_data.onSubmited = () => this.dismiss_modal_form()
        form_data.onCancel = () => this.dismiss_modal_form()

        return form_data
    }

    handle_edit_click = (data, e) => {
        this.setState({ is_show_modal_form: true, data_modal_form: this.get_edit_form(data) })
    }

    handle_role_click = (user, e) => {
        this.controller.get_role_form(user.id, user.user_role_id, (form_data) => {

            form_data.onSubmit = this.handle_role_form_submit
            form_data.onSubmited = () => this.dismiss_modal_form()
            form_data.onCancel = () => this.dismiss_modal_form()

            this.setState({ is_show_modal_form: true, data_modal_form: form_data })
        })
    }

    handle_password_click(user_id){
        this.setState({ is_show_modal_form: true, data_modal_form: this.get_password_form(user_id) })
    }

    handle_edit_form_submit = (data, success, error) => {
        this.controller.update(data, () => {
            success()
        }, error)
    }

    handle_password_form_submit = (data, success, error) => {
        this.controller.password(data, () => {
            success()
        }, error)
    }

    handle_reset_password_form_submit = (data, success, error) => {
        this.controller.reset_password(data, () => {
            success()
        }, error)
    }

    handle_role_form_submit = (data, success, error) => {
        this.controller.role(data, () => {
            success()
        }, error)
    }

    handle_reset_password_click(user_id){
        this.setState({ is_show_modal_form: true, data_modal_form: this.get_reset_password_form(user_id) })
    }

    handle_data_change = (data) => {
        if (this.state.is_self_trigger) {
            this.setState({ is_load: true })
        }else{
            this.setState({ data: data })
        }
        
    }

    handle_on_input_edited = (e) => {
        // e.preventDefault()
        if (!this.state.is_submiting) {
            var form_serialized = FormControl.serialize_form(this.form_list)
            
            if (form_serialized.should_submit) {

                this.controller.update({
                    data: form_serialized.result,
                    success: () => { 

                    },
                    error: (error_message) => { this.setState({ error_message: error_message }) },
                })
                
            }
        }
    }

    handle_block_click = (user_id) => {
        this.controller.block(user_id, () => this.refresh())
    }

    handle_unblock_click = (user_id) => {
        this.controller.unblock(user_id, () => this.refresh())
    }

    dismiss_modal_form(){
        this.setState({ is_show_modal_form: false })
    }

    refresh(){
        this.forceUpdate()
    }

    render() {

        const { 
            is_load,
            is_self_trigger,
            is_show_modal_form,
            data_modal_form,
        } = this.state

        let data = this.state.data

        if (is_self_trigger) {
            data = this.controller.read(this.state.id)
        }

        if (!is_load) {
            return (
                <div className="">
                    <Spinner position="center" />
                </div>
            )
        }

        return(

            <div className="account-profil">

                <div className="row">
                    <div className="col-12">
                        <div className="page-header">
                            <Beabcrumb
                                data_list={this.get_beabcrumb_data(data)}
                                />
                        </div>
                    </div>
                </div>

                <div className="row mt-30">
                    <div className="col-12 col-md-4 col-lg-3">
                        <div className="box box-white mb-20 account-metadata">
                            {/*profil picture, metadata*/}
                            <ProfilePictureControl 
                                lang={this.props.lang}
                                navigation={this.props.navigation}
                                img_url={this.props.ajax.site_url('account/image/' + data.id)}
                                className="profile-picture-lg"
                                is_editable={data.can_edit_image_profil}
                                upload_url={'account/image/' + data.id}
                                ajax={this.props.ajax}

                                 /> 

                            <h1>{data.view_name}</h1>

                            <div className="metadata">
                                <label>{this.props.lang.line('std_first_name')} : </label>
                                <span>{data.first_name} </span>
                            </div>

                            <div className="metadata">
                                <label>{this.props.lang.line('std_name')} : </label>
                                <span>{data.name}</span>
                            </div>

                            <div className="metadata">
                                <label>{this.props.lang.line('std_profil')} : </label>
                                <a href={this.props.ajax.site_url('account/' + data.user_name)}>/{data.user_name}</a>
                            </div>

                            <div className="metadata">
                                <label>{this.props.lang.line('std_initial')} : </label>
                                <span>{data.initial}</span>
                            </div>

                            {
                                data.user_category_id === null &&
                                <div className="metadata">
                                    <label>{this.props.lang.line('std_role')} : </label>
                                    <span>{data.user_role}</span>
                                </div>
                            }

                            {
                                data.user_category_id === null &&
                                <div className="metadata">
                                    <label>{this.props.lang.line('std_status')} : </label>
                                    <span>{data.is_blocked == 1 ? this.props.lang.line('std_disabled') : this.props.lang.line('std_activated')}</span>
                                </div>
                            }

                            {
                                data.is_editable &&
                                <div className="metadata">
                                    <ButtonEdit
                                        className="btn-block"
                                        onClick={(e) => this.handle_edit_click(data, e)}
                                        text={this.props.lang.line('std_edit')}
                                        />
                                </div>
                            }

                            {
                                data.user_category_id === null &&
                                <div>
                                    {
                                        data.can_block &&
                                        <div className="metadata">
                                            <ButtonBlock
                                                className="btn-block"
                                                onClick={(e) => this.handle_block_click(data.id, e)}
                                                text={this.props.lang.line('std_disable')}
                                                />
                                        </div>
                                    }

                                    {
                                        data.can_unblock &&
                                        <div className="metadata">
                                            <ButtonBlock
                                                className="btn-block"
                                                unblock={true}
                                                onClick={(e) => this.handle_unblock_click(data.id, e)}
                                                text={this.props.lang.line('std_activate')}
                                                />
                                        </div>
                                    }

                                    {
                                        data.can_update_role &&
                                        <div className="metadata">
                                            <button className="btn btn-block btn-primary" onClick={(e) => this.handle_role_click(data, e)}>
                                                <i className="fa fa-users"></i>
                                                {this.props.lang.line('std_role')}
                                            </button>
                                        </div>
                                    }

                                    {
                                        (data.can_change_password && !data.can_reset_password) &&
                                        <div className="metadata">
                                            <button className="btn btn-block btn-danger" onClick={(e) => this.handle_password_click(data.id, e)}>
                                                <i className="fa fa-key"></i>
                                                {this.props.lang.line('std_password')}
                                            </button>
                                        </div>
                                    }

                                    {
                                        data.can_reset_password &&
                                        <div className="metadata">
                                            <button className="btn btn-block btn-danger" onClick={(e) => this.handle_reset_password_click(data.id, e)}>
                                                <i className="fa fa-key"></i>
                                                {this.props.lang.line('std_reset_password')}
                                            </button>
                                        </div>
                                    }
                                </div>
                            }

                            
                        </div>
                    </div>
                    {
                        data.user_category_id === null &&
                        <div className="col-12 col-md-8 col-lg-9">
                            <div className="box box-white mb-20">
                                {/* time activity */}
                            </div>
                        </div>
                    }
                    <div className="col-12 col-md-7 col-lg-8">
                        <div className="box box-white mb-20 account-metadata">
                            {/* user metadata */}
                            <h3><i className="fa fa-info-circle"></i> {this.props.lang.line('std_about')}</h3>

                            {
                                data.view_metadata.map((metadata, index) => (
                                    <div className="metadata" key={index}>
                                        <label>{this.props.lang.line('std_' + metadata)} : </label>
                                        <span>{data[metadata]} </span>
                                    </div>
                                ))
                            }

                        </div>
                    </div>
                    {
                        data.user_category_id === null &&
                        <div className="col-12 col-md-5 col-lg-4">
                            <div className="box box-white mb-20">
                                {/* user devices */}
                            </div>
                        </div>
                    }
                </div>

                {
                    is_show_modal_form &&
                    <ModalForm
                        ajax={this.props.ajax}
                        lang={this.props.lang}
                        data={data_modal_form}
                        />
                }
            </div>
        )
    }
}

export default Read


